.weather-app {
  background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@keyframes gradientPulse {
    0% {
       transform: translateX(100vw);
    }
    100% {
      transform: translateX(-100vw);
    }
  }

  .gradient-text {
    font-size: 1.2rem;
    width: 100vw;
    position: fixed;
    top: 10px;
    background: linear-gradient(4deg, #022ece, #db8e00) ;
    -webkit-background-clip: text;
    color: transparent;
    animation: gradientPulse 10s infinite linear;
    margin-bottom: 10px;
  }
 
.sec-search>input,button{
  outline: none;
  /* display: none; */
  background-color: #50b9f6dd;
  border: 3px solid #79b9f9;
  color: #c2e9fb;
  padding: 5px 2px;
}
.sec-search>button{
  padding: 6px;
  background-color: #2b00ff;
  border: 4px solid #85d2fee9;
  font-size: .7rem;
  margin-left: 2px;
  cursor: pointer;
  transition: .3s linear;
}
.sec-search>button:hover{
  transform: translateX(3px);
  background-color: #2100c8;
  border: 4px solid #85d2fef7;
}
.weather-body {
  background-image: linear-gradient(120deg, #59d6ff76 0%, #8ec5fc 100%);
  padding: 15px;
  border-radius: 10px;
  width: 50%;
  height: 50%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
}

.info>h2{
  display: flex;
  align-items: center;
  color: rgb(0, 117, 175);
}

.dev-info{
bottom: 0;
  position: absolute;
  color: #00a1b3;
  font-family:Arial, Helvetica, sans-serif;
}
.info>.temp-sec>h1>span>sup>span{
  font-size: 1rem;
  cursor: pointer;
}
#wicon {
  font-size: 3rem;
  transition: 1s linear;
  animation: iconMove 6s infinite; /* Double the animation duration for slow motion */
  margin: 0;
  
}

@keyframes iconMove {
  0% {
    transform: translateX(0px);
    color: #00a1b3;
  }
  25%{
    transform: translateX(8px);
    color: #022ece;
  }
  50%{
    transform: translateX(16px);
    color: #00a1b3;
  }
  75%{
    transform: translateX(8px);
    color: #2b00ff;
  }
  100% {
    transform: translateX(0px);
    color: #00a1b3;
  }
}


.info>.temp-sec>h1{
  margin-top: 0;
}
.info>.buttom-sec{
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #004850;
  margin-top: 3rem;
  

}
.temp-sec{
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.temp-sec>.weather-info{
  font-size: .75rem;
  margin-left: 2rem;
}

.weather-info>p{
  margin: 0;
  margin-bottom: 3px;
}
#weather>h2{
  margin: 0;
  padding: 0;
  margin-bottom: 3px;
  color: #000000;
}
.info>.buttom-sec>p{
  display: inline-flex;
  align-items: flex-start;
  font-size: 1rem;
}
.info>.buttom-sec>p>.in-dec>p{
  padding: 0;
  margin: 0;
  font-size: .7rem;
  margin-left: 2px;
}
.info > .buttom-sec > p>.in-dec>p:nth-child(1)  {
  /* Additional styles for the first .in-dec > p element */
  /* color: red; */
  font-size: 1.3rem; /* Adjust the font size as needed */
  /* Add any other styling you want for the first element */
}
#c{
  color: white;
}

#notFound{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  text-align: center;

  
}
@media (max-width: 768px) {
  .weather-body {
    width: 70vw;
    background-color: #ffffff;
  }
}